.container {
  width: 600px;
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.container__color {
  width: 100%;
  text-align: center;
}

.container__text {
  font-size: 24px;
  font-weight: 700;
  display: inline-block;
}

.container__hex {
  font-size: 32px;
  font-weight: 700;
  display: inline-block;
}

.container__control {
  gap: 20px;
  display: flex;
}

button[data-stop], button[data-start] {
  width: 100px;
  letter-spacing: 2px;
  color: #000;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  font-weight: 700;
  transition: background-color .25s ease-in, color .25s ease-in;
}

button[data-stop] {
  background-color: #f14747;
}

button[data-stop]:hover {
  color: #fff;
  background-color: #0590a8;
}

button[data-start] {
  background-color: #ffd105;
}

button:hover {
  color: #fff;
  background-color: #1dac00;
}

